import React, { useState, useEffect } from 'react';
import Modal from 'react-bootstrap/Modal';

const AppModal = ({ modalData, modal, hideModal }) => {

    const { title: modalTitle, body: ModalBody } = modalData

    const [show, setShow] = useState(false);

    const handleClose = () => {
        setShow(false)
        hideModal()
    }

    const showModal = () => setShow(modal);

    useEffect(() => {
        showModal();
    });

    return (
        <>
            <Modal show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>
                        <h4>{modalTitle}</h4>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <ModalBody modalData={modalData} handleClose={handleClose} />
                </Modal.Body>
            </Modal>
        </>
    );
}


export default AppModal

