import React, { useEffect } from "react"
import PageTitle from "app/components/common/PageTitle"
import { useSelector, useDispatch } from "react-redux"
import { fetch as fetchVendor } from "features/VendorSlice";
import PageContent from "app/components/common/PageTable";
import VendorTableContent from "./VendorTableContent";


const action = {
    hasAction: true,
    actionTitle: 'Add Vendor',
    actionLink: '/vendor/create'
}

const VendorList = () => {

    const dispatch = useDispatch()

    const { data: vendors, status } = useSelector((state) => state.vendor)

    useEffect(() => {
        dispatch(fetchVendor())
    }, [dispatch])

    const VendorTable = ({ items, status }) => {
        return (
            <div className="table-wrapper table-responsive mt-4">
                <table className="table">
                    <tbody>
                        {items?.length > 0 &&
                            items?.map((item) => (
                                <VendorTableContent key={item.id} item={item} status={status} />
                            ))}
                    </tbody>
                </table>
            </div>
        );
    }

    return (
        <React.Fragment>
            <PageTitle title='Vendor List' action={action} />
            <PageContent items={vendors} status={status} component={VendorTable} />
        </React.Fragment>
    )
}

export default VendorList

