import React from "react"
import { useDispatch } from "react-redux"
import { remove as deleteCategory } from "features/ProductCategorySlice";
import { RemoveButton, CancelButton } from "app/components/utils/buttons";


const Delete = (props) => {

    const dispatch = useDispatch()

    const { handleClose, modalData } = props

    const { id, name } = modalData.data

    const deleteItem = () => dispatch(deleteCategory(id)) && handleClose()

    return (
        <div className="accordion-body">
            <h4>Are you sure to delete {name} ?</h4>
            <div className="d-grid gap-2 d-md-flex justify-content-md-end mt-4">
                <RemoveButton handleRemove={() => deleteItem(id)} />
                <CancelButton handleCancel={handleClose} />
            </div>
        </div>
    )
}

export default Delete